<template>
  <div>

    <v-container>
      <h1>{{ $route.params.collection }}</h1>
    </v-container>

    <div v-for='quote in quotes_by_collection' :key='quote.text'>
      <DuneQuote :quote='quote'/>
      <v-divider/>
    </div>

  </div>
</template>

<script>
import DuneQuote from '@/components/DuneQuote.vue'

export default {

  name: 'CollectionView',

  components: {
    DuneQuote
  },

  props: {
    quotes: {
      type: Array,
      default: () => []
    }
  },

  computed: {

    quotes_by_collection() {
      return this.quotes.filter(
        quote => quote.collection == this.$route.params.collection
      )
    }

  }

}
</script>
