<template>
  <div>

    <v-container>
      <h1>{{ $route.params.tag }}</h1>
    </v-container>

    <div v-for='quote in quotes_by_tag' :key='quote.text'>
      <DuneQuote :quote='quote'/>
      <v-divider/>
    </div>

  </div>
</template>

<script>
import DuneQuote from '@/components/DuneQuote.vue'

export default {

  name: 'TagView',

  components: {
    DuneQuote
  },

  props: {
    quotes: {
      type: Array,
      default: () => []
    }
  },

  computed: {

    quotes_by_tag() {
      return this.quotes
                 .filter(quote => quote.tags.includes(this.$route.params.tag))
    }

  }

}
</script>
